
<template>
  <nav>
    <v-app-bar flat app class="sd_br" color="#F4F4F4">
      <v-app-bar-nav-icon class="" elevation-10 @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title class="">
        <span class=" mr-3 wider"></span>
        <span>Coronavirus Disease (COVID-19) DashBoard</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>

      <v-btn color="log_btn white--text" @click="dialog=true" v-show="!hasLogin" class="mr-3">
        <v-icon class="mr-2">fa-user-circle</v-icon>/
      </v-btn>
      <v-btn color="log_btn  white--text" @click="dialog2=true" v-show="!hasLogin" class="mr-3">
        <v-icon class="mr-2">fa-user-circle</v-icon>
      </v-btn>
      <v-btn  @click="logout" v-show="hasLogin" class="mr-3">
        <v-icon class="mr-2">fa-user-circle</v-icon>
      </v-btn>

      <v-dialog v-model="dialog" persistent max-width="490">
        <v-card>
          <v-card-title class="headline">
            <v-select
              outlined
              v-model="selectedOp"
              label="/"
              :items="opList"
              prepend-icon="fa-info"
              autofocus
            ></v-select>
          </v-card-title>
          <v-card-text>
            <v-form ref="form" v-model="valid">
              <v-text-field
                v-model="username"
                :counter="10"
                :rules="[v => !!v || 'Not empty!']"
                label=""
                required
              ></v-text-field>
              <v-text-field
                v-model="password"
                :counter="15"
                :rules="[v => !!v || 'Not empty!']"
                label=""
                required
                type="password"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="success" @click="clean1" class="mr-4"></v-btn>
            <v-btn color="success" :disabled="!valid" @click="validate1" class="mr-4"></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialog2" persistent max-width="490">
        <v-card>
          <v-card-title class="headline">
            <v-select
              outlined
              v-model="selectedOp2"
              label=""
              :items="opList2"
              prepend-icon="fa-info"
              autofocus
            ></v-select>
          </v-card-title>
          <v-card-text>
            <v-form ref="form" v-model="valid2">
              <v-text-field
                v-model="username2"
                :counter="10"
                :rules="[v => !!v || 'Not empty!']"
                label=""
                required
              ></v-text-field>
              <v-text-field
                v-model="password2"
                :counter="15"
                :rules="[v => !!v || 'Not empty!']"
                label=""
                required
                type="password"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="success" @click="clean2" class="mr-4"></v-btn>
            <v-btn color="success" :disabled="!valid2" @click="validate2" class="mr-4"></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-app-bar>
    <v-navigation-drawer app v-model="drawer" class="primary">
      <v-list shaped class="primary">
        <v-list-item v-for="link in getFilteredLinks" :key="link.text" router :to="link.route">
          <!---->
          <v-list-item-action>
            <v-icon class="white--text">{{link.icon}}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title class="white--text">{{link.text}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-dialog
      v-model="alert"
      persistent
      :overlay="false"
      max-width="300px"
      transition="dialog-transition"
    >
      <v-card>
        <v-card-title primary-title></v-card-title>
        <v-card-text>{{alertText}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="alert=false" class="mr-4"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </nav>
</template>

<script>
import { store } from "../store/index";
import Bmob from "hydrogen-js-sdk";
export default {
  mounted() {},

  computed: {
    getFilteredLinks() {
      return store.getters.getFilteredLinks;
    },
    hasLogin() {
      return store.getters.hasLogin;
    },
    reqlog(){
      return store.getters.reqlog
    }
  },

  data() {
    return {
      drawer: true,

      selectedOp: "",
      opList: ["", ""],
      valid: false,
      dialog: false,
      username: "",
      password: "",

      selectedOp2: "",
      opList2: ["", ""],
      valid2: false,
      dialog2: false,
      username2: "",
      password2: "",

      alert: false,
      alertText: ""
    };
  },

  methods: {
    vxt() {
      console.log(store.getters.getFilteredLinks, this.opList);
      // this.showTips("");
      console.log(store.getters.getUserInfo);
    },
    clean1() {
      this.valid = false;
      this.dialog = false;
      this.username = "";
      this.password = "";
    },
    clean2() {
      this.valid2 = false;
      this.dialog2 = false;
      this.username2 = "";
      this.password2 = "";
    },
    validate1() {
      var g2_user = Bmob.Query("g2_user");
      if (this.selectedOp == "") {
        g2_user.set("username", this.username);
        g2_user.set("password", this.password);
        g2_user.set("role", "visitor");
        g2_user.set("realm", "None");
        g2_user
          .save()
          .then(res => {
            console.log(res);
            store.commit("login", {
              username: this.username,
              role: "visitor",
              realm: 'None'
            });
            this.clean1();
            this.showTips("");
          })
          .catch(err => {
            console.log(err);
            this.showTips("");
          });
      } else {
        g2_user.equalTo("username", "==", this.username);
        g2_user.equalTo("password", "==", this.password);
        g2_user.find().then(res => {
          if (res.length == 0) {
            this.showTips("");
          } else {
            store.commit("login", {
              username: this.username,
              role: "visitor",
              realm: 'None'
            });
            this.clean1();
            this.showTips("");
          }
        });
      }
    },
    validate2() {
      if (this.selectedOp2 == "") {
        this.axios
          .get("api/api/" + this.username2)
          .then(res => {
            if (res.data.length == 0) this.showTips("");
            else {
              var data = res.data[0]; // 
              if (data.password != this.password2) {
                this.showTips("");
              } else {
                this.showTips("");
                store.commit("login", {
                  username: this.username2,
                  role: "super_admin",
                  realm:'All'
                });
              }
            }
            this.clean2();
          })
          .catch(err => {
            console.log("oops," + err);
          });
      } else {
        // console.log()
        var g2_user = Bmob.Query("g2_user");
        g2_user.equalTo("username", "==", this.username2);
        g2_user.find().then(res => {
          if (res.length == 0) this.showTips("");
          else {
            var data = res[0];
            console.log(data.password);
            console.log(this.password2);
            console.log(data.password != this.password2);
            if (data.password != this.password2) this.showTips("");
            else {
              store.commit("login", {
                username: this.username2,
                role: "prov_admin",
                realm: data.realm
              });

              this.showTips("");
            }
          }
          this.clean2();
        });
      }
    },
    logout() {
      store.commit("logout");
    },

    showTips(text) {
      this.alertText = text;
      this.alert = true;
    }
  },

  watch:{
    reqlog:{
      handler(v){
        if(v){
          this.dialog=true
          store.commit('dereq')
        }
      }
    }
  }
};
</script>


<style scoped>
.sd_br{
  border-radius: 20px;
}

.wider{
  letter-spacing: 3px;
}
</style>